import { Component, ReactElement } from "react";
import { render } from "react-dom";
import { Renderer } from "meiosis";
import { meiosisRender, MeiosisRender, RenderIntoElement } from "meiosis-render";

function renderIntoElement(element: Element): Renderer<ReactElement<any>> {
  return function(view: ReactElement<any>): void {
    render(view, element);
  };
}

const renderer: MeiosisRender<ReactElement<any>> = meiosisRender(renderIntoElement);

export { renderer };



